	module.exports = {"data":{"post":{"html":"<p>Expatica has a feature on <a href=\"http://www.expatica.com/germany.asp?pad=190,230,&amp;item_id=24004\">Katherina Reiche</a>, the young (28) CDU* MdB (member of the Bundestag) whom Edmund Stoiber has selected for his compentency team (shadow cabinet) for family and social affairs. She's smart, well-spoken, looks good on camera, and also pregnant with her second child and not married, and therefore controversial in her own party. At least those parts of her party who haven't yet advanced out of the Stone Age.</p>\n<p>*I originally said she was in the CSU. Freudian slip. Stoiber's mind games must be working on me! \"Keep your eye on the pendulum and repeat after me: There is no difference between the CSU and CDU. There is no difference between ...\"</p>","fields":{"title":"Young Pregnant Single Mom For Family Ministry","date":"16 Jul 2002","url":"https://papascott.de/2002-07-16-young-pregnant-single-mom-for-family-ministry/"}}},"pathContext":{"slug":"/2002-07-16-young-pregnant-single-mom-for-family-ministry/","prev":{"fields":{"title":"Never Mind!","date":"16 Jul 2002","slug":"/2002-07-16-never-mind/"}},"next":{"fields":{"title":"Another One Bites The Dust","date":"18 Jul 2002","slug":"/2002-07-18-another-one-bites-the-dust/"}}}}

